import React from 'react'
import "../Css/Skills.css"
import Particles from 'react-particles-js';
export default function Skills() {
    const particlesOptions = {
        particles: {
            number: {
                value: 100,
                density: {
                    enable: true,
                    value_area: 800
                }
            },
            line_linked: {
                color: '#87CEFA',
                opacity: 1,
                "width": 3
            }
        }
    }
    return (
        <div className="SkillsSection" id="skillStart">
            <Particles className='particles'
                params={particlesOptions}
            />
            <div className="skill-text"><h1>SKILLS</h1>  </div>
            <div className="skill-icons">
                <img className="size" src="https://img.icons8.com/color/150/000000/c-plus-plus-logo.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/c-programming.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/javascript.png" alt="" />
                <img className="size" src="https://img.icons8.com/nolan/150/react-native.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/adobe-photoshop.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/figma.png" alt="" />
                <img className="size" src="https://img.icons8.com/bubbles/150/000000/react.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/css3.png" alt="" />
                <img className="size" style={{ color: "red" }} src="https://img.icons8.com/windows/150/000000/node-js.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/angularjs.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/postgreesql.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/mongodb.png" alt="" />
                <img className="size" src="https://img.icons8.com/color/150/000000/bootstrap.png" alt="" />
            </div>
        </div>
    )
}
