import React from 'react'
import "../Css/Navbar.css"
import profile from "../Components/profile.jpg"
export default function Navbar() {
    return (
        <>
            <nav className="active">
                <div className="hero">
                    <img src={profile} alt="" />
                </div>
                <div>
                    <h1 className="hero-text">Akash Tureha</h1>
                </div>
                <div className="social">
                    <div className="icons"><img src="https://img.icons8.com/fluent/120/000000/instagram-new.png" alt="" /></div>
                    <div className="icons"><img src="https://img.icons8.com/dusk/120/000000/github.png" alt="" /></div>
                    <div className="icons"><img src="https://img.icons8.com/fluent/120/000000/twitter.png" alt="" /></div>
                    <div className="icons"><img src="https://img.icons8.com/fluent/120/000000/facebook-new.png" alt="" /></div>
                </div>
                <div className="nav-bar">
                    <ul className="nav-list">
                        <li> <a href="#homeStart">Home</a> </li>
                        <li> <a href="#aboutStart">About</a> </li>
                        <li> <a href="#skillStart">Skills</a> </li>
                        <li> <a href="#portfolio">Portfolio</a> </li>
                        <li> <a href="#contactform">Contact</a> </li>
                    </ul>
                </div>
            </nav>
            <div className="mobile" onClick={() => NavEnable()}><img src="https://img.icons8.com/cotton/64/000000/menu.png" alt="" /></div>
        </>
    )
}

function NavEnable() {
    const nav = document.querySelector("nav");
    nav.classList.toggle("active");
    console.log(nav);
}