import React from 'react'
import "../Css/Home.css"
import Typed from 'typed.js';
import { imageSlider } from "./imageSlider"

export default function Home() {
    return (
        <>
            <div id="homeStart"></div>
            <div className="home" style={{
                height: "100vh", width: "100vw",
                backgroundImage: `url(${imageSlider[0]})`,
            }}>
                <div className="hero-container" data-aos="fade-in">
                    <h1 className="home-text">Akash Tureha</h1>
                    <p className="home-para">I'm <span className="typed " > </span>
                    </p>
                </div>
            </div>
        </>
    )
}
window.onload = function () {
    new Typed('.typed', {
        strings: ["Game Designer.", "Web Developer.", " Game Developer.", " Hard Core Gamer."],
        typeSpeed: 80,
        backSpeed: 80,
        loop: true,
    });
}







