import React from 'react'
import "../Css/Contactform.css"
function Contactform() {
    return (
        <div className="contact" id="contactform">
            <form className="form">
                <h2>CONTACT ME</h2>
                <p type="Name:"><input placeholder="Enter Name"></input></p>
                <p type="Message:"><input placeholder="Enter Email"></input></p>
                <p type="Email:"><input placeholder="Message"></input></p>
                <button>Send Message</button>
            </form>

            <div className="links" >
                <p>Quick Links</p>
                <a href="#homeStart">Home</a>
                <a href="#aboutStart">About</a>
                <a href="#skillStart">Skills</a>
                <a href="#portfolio">Portfolio</a>
                <a href="#homeStart">Services</a>

            </div>
            <div className="links" >
                <p>Explore</p>
                <a href="https://mrmischievousx.github.io/Tetris/">Tetris</a>
                <a href="https://mrmischievousx.github.io/Asteroid-Hunter/">Asteroid-Hunter</a>
                <a href="https://mrmischievousx.github.io/Sorting-Visualizer/">Sorting-Visualizer</a>
                <a href="https://mrmischievousx.github.io/Tic-Tac-Toe/">Tic-Tac-Toe</a>
                <a href="https://mrmischievousx.github.io/Smash-the-Balls/">Smash-the-Balls</a>
            </div>
        </div>

    )
}

export default Contactform

